.campus{
    margin: 80px auto;
    width: 90%;
    text-align: center;
}

.gallery{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 40px;
}

.gallery img{
    width: 23%;
}

/* --- Media Query --- */

@media (max-width: 800px){
    .gallery{
        flex-wrap: wrap;
      
    }
    .gallery img{
        width: 40%;
        margin: 20px;
    }
}