.footer{
    margin: 10px auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid gray;
    padding: 15px 0;
}

.footer ul li{
    list-style: none;
    display: inline-block;
    margin: 20px;
}

/* --- Media Query --- */

@media (max-width: 800px){
    .footer{
        display: block;
        text-align: center;
    }

    .footer ul li{
        margin: 10px;
    }

    .footer ul {
        margin-top: 10px;
    }
}